/* VARIABLES */

//colors

$blue: #2C68C9;
$blue-pale: #E0E8F5;
$blue-strong: #6B95D9;
$blue-dark: #ABC3E9;
$blue-medium: #D5E1F4;
$blue-light: #EAF0FA;
$gray-dark: #091528;
$gray-90: #F9FAFB;
$gray-60: #4A4F54;
$gray-30: #828A92;
$gray-cool: #BBC6D2;
$gray-pale: #F1F3F6;
$sea-green: #1BA39C;
$white: #ffffff;

$secondary-light-30: #5FBFBA;
$secondary-light-60: #A4DAD7;
$secondary-light-80: #D1EDEB;
$secondary-light-90: #E9F6F6;
$neutral-light-60:   #E3E8ED;
$neutral-light-30:   #CFD7DF;
$neutral-dark-30:    #828A92;
$neutral-dark-60:    #4A4F54;
$neutral-dark-80:    #25272A;

$semantic-error-red: #D7223B;
$semantic-error-red-light-30: #E36476;

$ld-form-title: #4C8EC3;
$ld-blue: #005DAA;
$ld-progress-bar: #BFD22B;
$ld-blue-light: #E5EFF6;
$ld-blue-pale: #D9E7F2;

// These color names should be no longer used
// as their names don't match the one on the designs
$sea-secondary: $secondary-light-30;
$blue-lines: $secondary-light-80;
$gray-80: $neutral-dark-80;


// Breakpoints
$desktop: 1440px;
$tablet: 1024px;
$mobile: 375px;

$mobile-min-height: 670px;

// Max-widths

$desktop-width: 1136px;
$tablet-width: 984px;
$mobile-width: 335px;

// colors
$primary-shade: rgb(255, 78, 46);
$primary-light: lighten($primary-shade, 37%);
$primary-dark: darken($primary-shade, 12%);
$accent-shade: #0079c0;
$accent-light: lighten($accent-shade, 37%);
$accent-dark: darken($accent-shade, 12%);
// Reassign color vars to semantic color scheme
$red: #dc3545;
$yellow: #ffc107;
$green: #28a745;
$cyan: #17a2b8;
$white: #fff;
$gray-100: #f8f9fa;
$gray-800: #343a40;
$theme-colors: (
  primary: $accent-shade,
  secondary: $primary-shade,
  success: $green,
  info: $cyan,
  warning: $yellow,
  danger: $red,
  light: $gray-100,
  dark: $gray-800,
);
$brand-primary: $accent-shade;
//$brand-success: $green;
//$brand-info: $teal;
//$brand-warning: $orange;
//$brand-danger: $red;
$brand-inverse: $primary-shade;
// Body
//
// Settings for the `<body>` element.
$body-bg: $white;
$body-color: $gray-800;
$inverse-bg: $primary-shade;
$inverse-color: $white;
// Links
//
// Style anchor elements.
$link-color: $accent-shade;
$link-decoration: none;
$link-hover-color: $accent-dark;
$link-hover-decoration: underline;
// Comments
$comment-spacer-x: 1.25em;
$comment-spacer-y: 1.25em;
// Responsive font sizes
$enable-responsive-font-sizes: true;
$enable-shadows: false;
$enable-gradients: false;


// Font variables

$font-general:   'Proxima Nova', sans-serif;
$font-family-base: $font-general;
$font-size-base: 1rem;
$font-size-lg: $font-size-base * 1.25;
$font-size-sm: $font-size-base * .875;

// font sizes

//Desktop
$font-44: 44px;
$font-36: 36px;
$font-32: 32px;
$font-24: 24px;
$font-20: 20px;
$font-16: 16px;
$font-12: 12px;

//Mobile
$font-40: 40px;
$font-28: 28px;
